a{
	text-decoration: none;
	text-align: center;
}
header{
	background: rgb(2,80,128);
	height:7em;
	width:100%;
	position:fixed;
	top:0;
	-webkit-box-shadow: 0px 1px 5px rgba(0,0,0,0.8);
	        box-shadow: 0px 1px 5px rgba(0,0,0,0.8);
	display:-ms-flexbox;
	display:flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	z-index:999999;
}
header>div{
	text-align: center;
}

.header-content{
	width:100%;
	display:-ms-flexbox;
	display:flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	position:relative;
}
.fa-chevron-left,
.fa-chevron-right{
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	cursor:pointer;
	color:white;
	-webkit-transition:color 0.2s, -webkit-box-shadow 0.2s ease-in-out;
	transition:color 0.2s, -webkit-box-shadow 0.2s ease-in-out;
	-o-transition:color 0.2s, box-shadow 0.2s ease-in-out;
	transition:color 0.2s, box-shadow 0.2s ease-in-out;
	transition:color 0.2s, box-shadow 0.2s ease-in-out, -webkit-box-shadow 0.2s ease-in-out;
	border-radius:50%;
	width:70px;
	height:70px;
	padding-top:12px;
}
.fa-chevron-left{
	padding-right:6px;
}
.fa-chevron-right{
	padding-left:6px;
}

.fa-chevron-left:hover,
.fa-chevron-right:hover{
	cursor:pointer;
	color:#a0dfff;
	-webkit-box-shadow: inset 0px 0px 0px 4px #a0dfff;
	        box-shadow: inset 0px 0px 0px 4px #a0dfff;
}

.header-text{
	font-family: 'Barlow Semi Condensed', sans-serif;
	font-weight:800;
	letter-spacing: 0.1em;
	font-size: 1.3em;
	color:white;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}
.header-sub-text{
	font-family: 'Barlow Semi Condensed', sans-serif;
	font-size: 1.2em;
	color:white;
	letter-spacing: 0.04em;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

@media screen and (min-width:350px) {
	.header-text{
		font-size: 1.6em;
	}
	.header-sub-text{
		font-size:1.6em;
	}
}

@media screen and (min-width:450px) {
	.header-text{
		font-size: 1.95em;
	}
	.header-sub-text{
		font-size:2.2em;
	}
}

@media screen and (min-width:600px) {
	.header-content{
		width:600px;
	}
}

@media screen and (min-width:800px) {
	.header-content{
		width:75%;
	}
}



